import React, { useState, useEffect } from 'react';
import { useAuth } from "../../common/context/AuthContext";
import AdminNavbar from '../components/AdminNavbar';
import { SkeletonFullPage } from '../components/skeletons';
import { 
  UnifiedBackground, 
  UnifiedCard, 
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import commissionService, { COMMISSION_CONFIG } from '../../services/commissionService';
import { 
  Wallet, 
  DollarSign, 
  TrendingUp, 
  Award,
  Users,
  Building2,
  Clock, 
  CheckCircle,
  XCircle,
  AlertCircle,
  Calendar,
  Filter,
  Download,
  RefreshCw,
  Target,
  BarChart3,
  PieChart,
  Activity
} from 'lucide-react';

function AffiliateCommissions() {
  const { user } = useAuth();
  useScrollAnimation();

  // State management
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('overview');
  const [adminData, setAdminData] = useState({
    wallet_balance: 0,
    total_received: 0,
    unclaimed_total: 0,
    commission_count: 0,
    recent_commissions: []
  });
  const [commissionHistory, setCommissionHistory] = useState([]);
  const [statistics, setStatistics] = useState({});
  const [filters, setFilters] = useState({
    level: '',
    status: '',
    promoterId: '',
    dateFrom: '',
    dateTo: ''
  });
  const [toast, setToast] = useState({ show: false, message: '', type: '' });

  // Toast utility
  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
    setTimeout(() => {
      setToast({ show: false, message: '', type: '' });
    }, 4000);
  };

  // Load all data
  const loadData = async () => {
    try {
      setLoading(true);
      
      // Load admin summary
      const adminResult = await commissionService.getAdminCommissionSummary();
      if (adminResult.success) {
        setAdminData(adminResult.data);
      }
      
      // Load commission history
      const historyResult = await commissionService.getCommissionHistory(filters);
      if (historyResult.success) {
        setCommissionHistory(historyResult.data);
      }
      
      // Load statistics
      const statsResult = await commissionService.getCommissionStatistics();
      if (statsResult.success) {
        setStatistics(statsResult.data);
      }
      
      console.log('üìä Admin commission data loaded');
      
    } catch (error) {
      console.error('‚ùå Error loading admin commission data:', error);
      showToast('Failed to load commission data', 'error');
    } finally {
      setLoading(false);
    }
  };

  // Load data on component mount
  useEffect(() => {
    loadData();
  }, []);

  // Handle refresh
  const handleRefresh = () => {
    loadData();
    showToast('Commission data refreshed', 'success');
  };

  // Handle filter change
  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  // Apply filters
  const applyFilters = () => {
    loadData();
  };

  // Get status display
  const getStatusDisplay = (status) => {
    switch (status) {
      case 'credited':
        return { icon: CheckCircle, color: 'text-green-400', bg: 'bg-green-500/20', label: 'Credited' };
      case 'pending':
        return { icon: Clock, color: 'text-yellow-400', bg: 'bg-yellow-500/20', label: 'Pending' };
      case 'failed':
        return { icon: XCircle, color: 'text-red-400', bg: 'bg-red-500/20', label: 'Failed' };
      default:
        return { icon: AlertCircle, color: 'text-gray-400', bg: 'bg-gray-500/20', label: 'Unknown' };
    }
  };

  // Format currency
  const formatCurrency = (amount) => {
    return `‚Çπ${parseFloat(amount).toLocaleString('en-IN')}`;
  };

  // Show loading skeleton
  if (loading) {
    return (
      <>
        <SharedStyles />
        <AdminNavbar />
        <UnifiedBackground>
          <SkeletonFullPage type="dashboard" />
        </UnifiedBackground>
      </>
    );
  }

  return (
    <>
      <SharedStyles />
      <AdminNavbar />
      <UnifiedBackground>
        <div className="min-h-screen p-8">
          <div className="w-full px-4">
            
            {/* Header */}
            <div className="flex justify-between items-center mb-8" data-animate>
              <div>
                <h1 className="text-4xl font-bold text-white mb-2">Affiliate Commissions</h1>
                <p className="text-gray-300">Monitor and manage affiliate commission distributions</p>
              </div>
              <UnifiedButton
                onClick={handleRefresh}
                className="flex items-center space-x-2"
              >
                <RefreshCw className="w-5 h-5" />
                <span>Refresh</span>
              </UnifiedButton>
            </div>

            {/* Admin Wallet Summary */}
            <div className="space-y-8" data-animate>
                
                {/* Admin Wallet Summary */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                  
                  {/* Admin Wallet Balance */}
                  <UnifiedCard className="p-6 bg-gradient-to-br from-green-500/10 to-emerald-500/10 border border-green-500/30 hover:border-green-400/50 transition-all duration-300">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-green-300 text-sm font-semibold uppercase tracking-wider">Admin Wallet</p>
                        <p className="text-3xl font-bold text-white mt-2">{formatCurrency(adminData.wallet_balance)}</p>
                      </div>
                      <div className="w-12 h-12 bg-green-500/20 rounded-xl flex items-center justify-center">
                        <Wallet className="w-6 h-6 text-green-400" />
                      </div>
                    </div>
                    <div className="flex items-center mt-4 text-sm">
                      <div className="w-2 h-2 bg-green-400 rounded-full mr-2"></div>
                      <span className="text-green-300">Available balance</span>
                    </div>
                  </UnifiedCard>

                  {/* Total Received */}
                  <UnifiedCard className="p-6 bg-gradient-to-br from-orange-500/10 to-yellow-500/10 border border-orange-500/30 hover:border-orange-400/50 transition-all duration-300">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-orange-300 text-sm font-semibold uppercase tracking-wider">Total Received</p>
                        <p className="text-3xl font-bold text-white mt-2">{formatCurrency(adminData.total_received)}</p>
                      </div>
                      <div className="w-12 h-12 bg-orange-500/20 rounded-xl flex items-center justify-center">
                        <TrendingUp className="w-6 h-6 text-orange-400" />
                      </div>
                    </div>
                    <div className="flex items-center mt-4 text-sm">
                      <div className="w-2 h-2 bg-orange-400 rounded-full mr-2"></div>
                      <span className="text-orange-300">Lifetime earnings</span>
                    </div>
                  </UnifiedCard>

                  {/* Unclaimed Commissions */}
                  <UnifiedCard className="p-6 bg-gradient-to-br from-purple-500/10 to-pink-500/10 border border-purple-500/30 hover:border-purple-400/50 transition-all duration-300">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-purple-300 text-sm font-semibold uppercase tracking-wider">Unclaimed</p>
                        <p className="text-3xl font-bold text-white mt-2">{formatCurrency(adminData.unclaimed_total)}</p>
                      </div>
                      <div className="w-12 h-12 bg-purple-500/20 rounded-xl flex items-center justify-center">
                        <Building2 className="w-6 h-6 text-purple-400" />
                      </div>
                    </div>
                    <div className="flex items-center mt-4 text-sm">
                      <div className="w-2 h-2 bg-purple-400 rounded-full mr-2"></div>
                      <span className="text-purple-300">Fallback commissions</span>
                    </div>
                  </UnifiedCard>

                </div>

              </div>

            {/* Commission History Tab - REMOVED */}
            {false && (
              <div className="space-y-6" data-animate>
                
                {/* Filters */}
                <UnifiedCard className="p-6" variant="glassDark">
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">Level</label>
                      <select
                        value={filters.level}
                        onChange={(e) => handleFilterChange('level', e.target.value)}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      >
                        <option value="">All Levels</option>
                        <option value="1">Level 1 (‚Çπ500)</option>
                        <option value="2">Level 2 (‚Çπ100)</option>
                        <option value="3">Level 3 (‚Çπ100)</option>
                        <option value="4">Level 4 (‚Çπ100)</option>
                        <option value="0">Admin Fallback</option>
                      </select>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">Status</label>
                      <select
                        value={filters.status}
                        onChange={(e) => handleFilterChange('status', e.target.value)}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      >
                        <option value="">All Status</option>
                        <option value="credited">Credited</option>
                        <option value="pending">Pending</option>
                        <option value="failed">Failed</option>
                      </select>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">Date From</label>
                      <input
                        type="date"
                        value={filters.dateFrom}
                        onChange={(e) => handleFilterChange('dateFrom', e.target.value)}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">Date To</label>
                      <input
                        type="date"
                        value={filters.dateTo}
                        onChange={(e) => handleFilterChange('dateTo', e.target.value)}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      />
                    </div>
                    <div className="flex items-end">
                      <UnifiedButton
                        onClick={applyFilters}
                        className="w-full flex items-center justify-center space-x-2"
                      >
                        <Filter className="w-4 h-4" />
                        <span>Apply Filters</span>
                      </UnifiedButton>
                    </div>
                  </div>
                </UnifiedCard>

                {/* Commission History Table */}
                <UnifiedCard className="overflow-hidden" variant="glassDark">
                  <div className="p-6 border-b border-gray-700/50">
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="text-2xl font-semibold text-white">Commission History</h3>
                        <p className="text-gray-300 text-sm mt-1">
                          All affiliate commission transactions
                        </p>
                      </div>
                      <div className="flex items-center space-x-3">
                        <span className="text-sm text-gray-400">
                          {commissionHistory.length} transactions
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <div className="overflow-x-auto">
                    {commissionHistory.length === 0 ? (
                      <div className="p-12 text-center">
                        <Activity className="w-16 h-16 text-gray-500 mx-auto mb-4" />
                        <h3 className="text-xl font-semibold text-gray-400 mb-2">No Commission History</h3>
                        <p className="text-gray-500">
                          Commission transactions will appear here once customers are created.
                        </p>
                      </div>
                    ) : (
                      <table className="w-full">
                        <thead className="bg-gray-800/50">
                          <tr>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Transaction</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Level</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Recipient</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Amount</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Status</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Date</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-700/50">
                          {commissionHistory.map((commission) => {
                            const statusDisplay = getStatusDisplay(commission.status);
                            const StatusIcon = statusDisplay.icon;
                            
                            return (
                              <tr key={commission.id} className="hover:bg-gray-800/30 transition-colors">
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="text-sm">
                                    <div className="text-white font-medium">
                                      {commission.transaction_id}
                                    </div>
                                    <div className="text-gray-400">
                                      Customer: {commission.customers?.name || 'N/A'}
                                    </div>
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="flex items-center">
                                    <div className={`w-8 h-8 rounded-full flex items-center justify-center text-white font-bold text-sm ${
                                      commission.level === 0 ? 'bg-gradient-to-r from-red-500 to-pink-500' : 'bg-gradient-to-r from-orange-500 to-yellow-500'
                                    }`}>
                                      {commission.level === 0 ? 'A' : commission.level}
                                    </div>
                                    <div className="ml-3">
                                      <span className="text-sm text-gray-300">
                                        {commission.level === 0 ? 'Admin Fallback' : `Level ${commission.level}`}
                                      </span>
                                    </div>
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="text-sm">
                                    <div className="text-white font-medium">
                                      {commission.recipient?.name || 'N/A'}
                                    </div>
                                    <div className="text-gray-400">
                                      {commission.recipient_type === 'admin' ? 'Admin' : 'Promoter'}
                                    </div>
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="text-lg font-bold text-green-300">
                                    {formatCurrency(commission.amount)}
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${statusDisplay.bg} ${statusDisplay.color} border border-current/30`}>
                                    <StatusIcon className="w-3 h-3 mr-1" />
                                    {statusDisplay.label}
                                  </span>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                  {new Date(commission.created_at).toLocaleDateString('en-IN', {
                                    year: 'numeric',
                                    month: 'short',
                                    day: 'numeric',
                                    hour: '2-digit',
                                    minute: '2-digit'
                                  })}
                                </td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    )}
                  </div>
                </UnifiedCard>

              </div>
            )}

          </div>
        </div>

        {/* Toast Notification */}
        {toast.show && (
          <div className={`fixed top-4 right-4 z-50 max-w-md p-4 rounded-lg shadow-lg border transition-all duration-300 ${
            toast.type === 'success' 
              ? 'bg-green-500/10 border-green-500/30 text-green-300' 
              : 'bg-red-500/10 border-red-500/30 text-red-300'
          }`}>
            <div className="flex items-start space-x-3">
              <div className={`w-6 h-6 rounded-full flex items-center justify-center ${
                toast.type === 'success' ? 'bg-green-500' : 'bg-red-500'
              }`}>
                {toast.type === 'success' ? (
                  <CheckCircle className="w-4 h-4 text-white" />
                ) : (
                  <XCircle className="w-4 h-4 text-white" />
                )}
              </div>
              <div className="flex-1">
                <p className="text-sm font-medium whitespace-pre-line">{toast.message}</p>
              </div>
              <button
                onClick={() => setToast({ show: false, message: '', type: '' })}
                className="text-gray-400 hover:text-white transition-colors"
              >
                <XCircle className="w-4 h-4" />
              </button>
            </div>
          </div>
        )}

      </UnifiedBackground>
    </>
  );
}

export default AffiliateCommissions;
